import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ListMedicineComponent } from './list-medicine.component';
import { ListMedicineRoutingModule } from './list-medicine-routing.module';
import { SharedModule } from '../../../shared/shared.module';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

@NgModule({
  imports: [
    CommonModule,
    ListMedicineRoutingModule,
    SharedModule,
    NgxDatatableModule,
    FormsModule, 
    ReactiveFormsModule,
    HttpModule
  ],
  declarations: [ListMedicineComponent]
})
export class ListMedicineModule { }



