import { Component, OnInit, ViewChild } from '@angular/core';
import { DatatableComponent } from '@swimlane/ngx-datatable';

import { GetPopularChemistService } from '../../services/GetPopularChemistService';
import { GetChemistDataService } from '../../services/GetChemistDataService';
import { AddAreaService } from '../../services/AddAreaService';
import { AddchemistService } from '../../services/AddChemistService';
import { ProductService } from '../../services/ProductService';

declare var jQuery: any;

@Component({
  selector: 'list-medicine',
  templateUrl: './list-medicine.component.html',
  styleUrls: [
    './list-medicine.component.scss',
    '../../../../assets/icon/icofont/css/icofont.scss'
  ],
  providers: [GetPopularChemistService, GetChemistDataService, AddAreaService, ProductService]
})
export class ListMedicineComponent implements OnInit {
  rowsBasic = [];
  fullScreenRow = [];
  loadingIndicator = true;
  reorderable = true;

  columns = [
    { prop: 'PharmacyName' },
    { prop: 'Address' },
    { prop: 'City.Name', name: 'City' },
    { prop: 'PharmaCommission', name: 'Commission' },
    { prop: 'RatingCount', name: 'Rating' },
    { name: "Actions", sortable: "false", prop: "id" }
  ];

  rows = [];
  expanded = {};
  timeout: any;

  rowsFilter = [];
  tempFilter = [];

  rowsFilter2 = [];
  tempFilter2 = [];

  rowsFilter3 = [];
  tempFilter3 = [];

  /**
   * Pharma Variables
   */
  chemists_array = [];
  medicines_array = [];
  PharmacistId: number = 0;
  public MedicineName: string = "";
  public Address: string = "";
  public ManufacturerName: string = "";
  public PharmaCommission: number = 0;
  public City: number = 0;
  public Area: number = 0;
  public SubCategoryId: number = 0;
  public CategoryId: number = 0;
  public ImageUrl: string = "";
  public DosageId: number = 0;
  public DosageSizeId: number = 0;
  public MeasurementId: number = 0;
  public DosageValueId: number = 0;
  public ProductPrice: number = 0;
  public ProductQuantity: number = 0;
  public Username: string = "";
  public Password: string = "";
  public Latitude: number = 0;
  public Longitude: number = 0;

  public cities_array = [];
  public areas_array = [];
  public chemists_categories = [];
  public chemists_sub_categories = [];
  public measurements_array = [];
  public dosages_array = [];
  public dosages_sizes_array = [];

  public MeasurementListId: number = 0;
  public measurement_final_ids = [];
  public variation_array = [];
  public variation_model = {
    Id: 0,
    measurement_id: 0,
    measurement_name: "",
    dosage_id: 0,
    dosage_name: "",
    dosage_value_id: 0,
    dosage_value_name: "",
    quantity: 0,
    price: 0
  }
  public measurement_model = {
    Id: 0,
    measurement_id: 0,
    measurement_name: "",
  }

  public runner: boolean = false;

  /**
   * Pharma Variables End
   */
  @ViewChild(DatatableComponent) table: DatatableComponent;
  @ViewChild(DatatableComponent) measurementtable: DatatableComponent;
  @ViewChild(DatatableComponent) variationstable: DatatableComponent;
  public showAttr: boolean = false;
  constructor(public _getPopularChemistService: GetPopularChemistService,
    public _getChemistDataService: GetChemistDataService,
    public _AddAreaNameService: AddAreaService,
    public _productService: ProductService) {
    var session_obj = JSON.parse(localStorage.getItem('session_obj'));
    if (session_obj.UserRole == 1) {
      this.getChemists();
      this.showAttr = true;
    }
    else if (session_obj.UserRole == 2) {
      this.PharmacistId = session_obj.ChemistId;
      this.showAttr = false;
      this.getProductsOfChemists();
    }
  }

  onSelect({ selected }) {
    console.log(selected);
  }

  onActivate(event) { }

  getChemists() {
    console.error("Fetching Chemists");
    this._getChemistDataService.getRegisteredChemistService().subscribe(
      response => {
        this.chemists_array = response.data;
      });
  }// End of Get Chemists

  getProductsOfChemists() {
    $('#medicines-fetcher').show();
    this._productService.getProductsFromChemistId(this.PharmacistId).subscribe(response => {
      console.log(response);
      $('#medicines-fetcher').hide();
      this.medicines_array = response.data;
      this.tempFilter = this.medicines_array;
      this.rowsFilter = this.medicines_array;
    });
  }

  view(Id) {
    console.log(Id);
    var medicine_obj = this.medicines_array.find(x=>x.Id == Id);
    console.log(medicine_obj);
    for(var i = 0; i < medicine_obj.ProductDosageAndSizeMappings.length; i++){
      this.variation_model = {
        Id: 0,
        measurement_id: 0,
        measurement_name: "",
        dosage_id: 0,
        dosage_name: "",
        dosage_value_id: 0,
        dosage_value_name: "",
        quantity: 0,
        price: 0
      }
      this.variation_model.Id = medicine_obj.ProductDosageAndSizeMappings[i].Id;
      this.variation_model.measurement_name = medicine_obj.ProductDosageAndSizeMappings[i].MeasurementName;
      this.variation_model.dosage_name = medicine_obj.ProductDosageAndSizeMappings[i].DosageName;
      this.variation_model.dosage_value_name = medicine_obj.ProductDosageAndSizeMappings[i].DosageValue;
      this.variation_model.price = medicine_obj.ProductDosageAndSizeMappings[i].Price;
      this.variation_model.quantity = medicine_obj.ProductDosageAndSizeMappings[i].Quantity;
      this.variation_array.push(this.variation_model);
    }
    this.MedicineName = medicine_obj.Name;
    this.ManufacturerName = medicine_obj.ManufacturedBy;
    this.rowsFilter3 = this.variation_array;
    jQuery('#detail').show();
    jQuery('#list').hide();
  }

  delete(Id) {
    console.log(Id);
  }

  ngOnInit(): void {

  }

  updateFilter(event) {
    const val = event.target.value.toLowerCase();
    console.log(val);
    // filter our data
    const temp = this.tempFilter.filter(function (d) {
      return d.Name.toLowerCase().indexOf(val) !== -1 || !val;
    });

    // update the rows
    this.rowsFilter = temp;
    // Whenever the filter changes, always go back to the first page
    this.table.offset = 0;
  }

  onPage(event) {
    clearTimeout(this.timeout);
    this.timeout = setTimeout(() => {
      console.log('paged!', event);
    }, 100);
  }
  getRowHeight(row) {
    return row.height;
  }

}



